import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	IconButton,
	Grid,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory } from 'react-router';
import { TOffer } from '../../../types';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import { getOfferPrice } from '../../../helpers/offer';
import { formatCurrency } from '../../../helpers/currency';
import { formatDate } from '../../../helpers/date';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

type Props = {
	offers: Array<TOffer>;
	isLoading: boolean;
	hasMore?: boolean;
	loadMore?: Function;
};

const OffersTable = ({ offers, isLoading, hasMore, loadMore }: Props) => {
	const classes = useStyles();
	const history = useHistory();

	const editOffer = (id: string) => {
		history.push(`/offer/${id}`);
	};

	if (_isEmpty(offers) && !isLoading) {
		return (
			<Grid item xs={12}>
				<Alert icon={false} severity='info'>
					No Records found
				</Alert>
			</Grid>
		);
	}

	return (
		<>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header />
					<TableBody>
						{offers.map(offer => {
							if (typeof offer === 'number') return null;

							return (
								<TableRow hover tabIndex={-1} key={offer.OfferId}>
									<TableCell component='th'>{offer.OfferNo}</TableCell>
									<TableCell align='left'>
										{_get(offer, 'PropertyData.FullAddress', '')}
									</TableCell>
									<TableCell align='left'>
										{_get(offer, 'PropertyData.OwnerNames', '')}
									</TableCell>
									<TableCell align='left'>{getOfferPrice(offer)}</TableCell>
									<TableCell align='left'>{offer.Funding?.PurchaseType}</TableCell>
									<TableCell align='left'>{offer.Funding?.LoanType}</TableCell>
									<TableCell align='left'>
										{formatCurrency(offer.Funding?.DownPayment)}
									</TableCell>
									<TableCell sx={{ pr: 3 }} align='left'>
										{offer.Step?.messages?.advisor}
									</TableCell>
									<TableCell align='left'>{formatDate(offer.CreatedAt)}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<IconButton color='primary' onClick={() => editOffer(offer.OfferId)}>
											<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
										</IconButton>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>

			{hasMore && loadMore && (
				<LoadMore onLoadMore={loadMore} loading={isLoading} />
			)}
		</>
	);
};

export default OffersTable;
